body {
  background: #fff;
  font-family: "Roboto", sans-serif;
  color: #18181b;
}

.container {
  position: relative;
  max-width: 1280px;
  box-sizing: content-box;
  margin: 0 auto;
  padding: 0 16px;
}

.section {
  padding: 64px 0;
  background: #fff;
}
.section-dark {
  background: #f4f4f5;
}
.section-title {
  display: grid;
  grid-template-columns: 1fr auto;
  gap: 20px;
  align-items: start;
  margin: 0 0 48px;
}
.section-title h2,
