  border-radius: 24px;
  padding: 10px 18px;
  box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
  background: #51525c;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.section-link__mob {
  display: none;
}

.nav__hum {
  display: none;
}
.nav__row {
  background: #18181b;
  padding: 8px 0;
}
.nav__row .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__logo {
