}
.nav__link {
  border: 1px solid #51525c;
  border-radius: 24px;
  padding: 10px 16px;
  background: #51525c;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.42857;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}
.nav__block {
  padding: 10px 0;
  border-bottom: 1px solid #e4e4e7;
}
.nav__block .nav__link {
  display: none;
}
.nav__block .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__list {
  display: flex;
  justify-content: start;
  align-items: center;
  gap: 0;
}
.nav__list a {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 1.5;
  color: #3f3f46;
  padding: 8px 12px;
  border-radius: 32px;
  transition: all 0.3s;
}
.nav__list a:hover {
  background: #fafafa;
}
.nav__list li {
