}
.nav__list li:nth-child(6) .nav__drop, .nav__list li:nth-child(7) .nav__drop, .nav__list li:nth-child(8) .nav__drop {
  right: 0;
  left: auto;
}
.nav__drop {
  display: grid;
  grid-template-columns: 1fr;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  left: 0;
  top: calc(100% + 20px);
  width: 640px;
  grid-template-columns: 336px 1fr;
  border: 1px solid #e4e4e7;
  border-radius: 12px;
  box-shadow: 0 4px 6px -2px rgba(16, 24, 40, 0.03), 0 12px 16px -4px rgba(16, 24, 40, 0.08);
  background: #fff;
  z-index: 20;
  transition: all 0.3s;
}
.nav__drop ol {
  gap: 8px;
  display: grid;
}
.nav__drop ol {
  padding: 24px 20px;
}
.nav__drop ol a {
  border-radius: 8px;
  padding: 12px;
  justify-content: start;
  align-items: center;
  gap: 16px;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 1.5;
  color: #51525c;
  width: 100%;
