}
.nav__drop ol a:hover {
  color: #18181b;
  background: #fafafa;
}
.nav__drop_bg {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fafafa;
  padding: 32px;
  width: 100%;
  height: 100%;
}
.nav__drop_bg img {
  width: 100%;
  height: 1px;
  min-height: 100%;
  border-radius: 6px;
  object-fit: cover;
}
.nav__drop.active {
  opacity: 1;
  pointer-events: all;
}
.nav__drop_btn {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}
.nav__drop_btn svg {
  transition: all 0.3s;
}
.nav__drop_btn.active svg {
  transform: rotate(180deg);
}
.nav__search {
  position: relative;
  width: 100%;
  max-width: 280px;
}
.nav__search a {
  display: block;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 14px;
}
.nav__search input {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  border: 1px solid #a0a0ab;
  border-radius: 24px;
  padding: 10px 14px 10px 42px;
  background: #fff;
  width: 100%;
  box-sizing: border-box;
}
.nav__search input::placeholder {
  color: #a0a0ab;
  overflow: hidden;
  text-overflow: ellipsis;
}

.header__slider {
  width: 100%;
  display: block;
}
.header__slider .swiper-wrapper {
  align-items: stretch;
}
.header__slider .swiper-slide {
  height: auto;
}
.header__slider img {
  width: 100%;
