  transition: all 0.3s;
}
.header__item p + p {
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.5;
  color: #a0a0ab;
  margin: 8px 48px 0 0;
}
.header__item span {
  position: absolute;
  right: 24px;
  bottom: 24px;
  border: 1px solid #e4e4e7;
  border-radius: 100%;
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}
.header__item span path {
  stroke: #A0A0AB;
  transition: all 0.3s;
}
.header__item:hover::before, .header__item.active::before {
  opacity: 1;
}
.header__item:hover p, .header__item.active p {
